import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';
import InviteFriend from '../../components/user/invite';
import User from '../../components/user';

@observer
class UserNotifications extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }
    async loadAll() {
        await this.loadNotifications();
        await this.markNotificationsAsRead();
    }

    async loadNotifications() {
        const { userStore } = this.props.stores;
        await userStore.getNotifications();
    }

    async markNotificationsAsRead() {
        const { userStore } = this.props.stores;
        const { notifications } = userStore;
        const ids = notifications.map(e => e.id);
        await userStore.markNotificationsAsRead({ ids });
    }

	search = async () => {
        const { userStore } = this.props.stores;
        const { searchText } = this.state;
        await userStore.searchUsers({ search: searchText });
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	componentDidMount() {
        this.loadAll();
	}

	render() {
        const { showDate } = this.state;
		const { userStore } = this.props.stores;
		const { notifications } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
                    {notifications && notifications.map(notification => {
                        const { addedBy: user  = {} } = notification;
                        const displayNameShort = user.firstname ? `${util.ucfirst(user.firstname, true)}${util.ucfirst(user.lastname, true)}` : `${util.ucfirst(user.email || '', true)}`;
                        const displayName = user.firstname ? `${user.firstname} ${user.lastname}` : `${user.email}`;
                        return (
                            <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-4 position-relative' style={`opacity: ${notification.read ? 0.5: 1};`}>
                                <div class={`text-center border rounded-circle imageRounded`} style={`padding-top: 10px; ${user.image ? `background-image: url("${user.image.s3SmallLink}");` : ''}`}>
                                   {!user.image && <span class='font-weight-lighter' style='font-size: 35px;'>{displayNameShort}</span>}
                                </div>
                                <div class='flex-grow-1 pl-3 pr-2'>
                                    <span class='float-right'>
                                        {notification.type && <span class='mr-2'>{notification.type}</span>}
                                    </span>
                                    <a href={notification.url} class='stretched-link'>
                                        <h5 style='line-height: 1.0em; margin-bottom: 0;'>{displayName}</h5>
                                    </a>
                                    <small class='text-muted'>
                                        {notification.date && <small onClick={this.toggleDate}>
                                            {showDate ? util.formatDate(notification.date, { locale: 'no-NB' }) : util.formatDistance(notification.date, new Date(), { locale: 'no-NB' })}
                                        </small>}
                                        <br />
                                    </small>
                                    {/* <small class='text-muted'>
                                        {dog.chipId && <span class='ml-0'><i class='fas fa-microchip' /> {dog.chipId}</span>}
                                        {dog.history && <span class='ml-2'><i class='fas fa-file-signature' /> {dog.history.length}</span>}
                                        {dog.images && <span class='ml-2'><i class='fas fa-image' /> {dog.images.length}</span>}

                                        {workoutSummary && workoutSummary.distanceKm > 0 && <span class='ml-2'><i class='fas fa-road' /> {util.format(workoutSummary.distanceKm, 0)}km</span>}
                                        {workoutSummary && workoutSummary.elevation > 0 && <span class='ml-2'><i class='fas fa-mountain' /> {util.format(workoutSummary.elevation, 0)}m</span>}
                                        {workoutSummary && workoutSummary.duration > 0 && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(workoutSummary.duration)}</span>}
                                        {workoutSummary && workoutSummary.speedAvg > 0 && <span class='ml-2'><i class='fas fa-tachometer-alt' /> {util.format(workoutSummary.speedAvg, 1)}km/t</span>}
                                    </small> */}
                                    {notification.message}
                                </div>
                            </div>
                        );
                    })}
				</div>
			</div>
		);
	}
}

export default UserNotifications;
